import React, { Component } from 'react'
import "./Home.css"
import Lunbo from '../../components/Lunbo/Lunbo'
import { NavLink } from 'react-router-dom'
import { Tabs,Carousel,Col, Row, Button, Divider } from 'antd';
const { TabPane } = Tabs;

export default class Home extends Component {
  state={
    lunbotu:[
      {id:1,tu:"banner1.jpg"},   
      {id:2,tu:"banner2.jpg"},
      {id:3,tu:"banner3.jpg"},
    ],
    hot:[
      {id:1,xxk:[
        {id:1,fmimg:"hot1.jpg",hotimg:"hot4.jpg"},
        {id:2,fmimg:"hot2.jpg",hotimg:"hot5.jpg"},
        {id:3,fmimg:"hot3.jpg",hotimg:"hot6.jpg"},
      ]
      },
      {id:2,xxk:[
        {id:1,fmimg:"hot7.png",hotimg:"hot11.png"},
        {id:2,fmimg:"hot8.png",hotimg:"hot12.png"},
        {id:3,fmimg:"hot9.png",hotimg:"hot13.png"},
      ]
      },
      {id:2,xxk:[
        {id:1,fmimg:"hot14.jpg",hotimg:"hot17.jpg"},
        {id:2,fmimg:"hot15.jpg",hotimg:"hot18.jpg"},
        {id:3,fmimg:"hot16.jpg",hotimg:"hot19.jpg"},
      ]
      },
    ], 
    isred:0,

    
  }
  componentDidMount(){
    document.documentElement.scrollTop = document.body.scrollTop =0;//跳转后回到顶部
  }
  xuan=(num)=>{
    this.setState({isred:num})
  }
  render() {
    const {lunbotu,isred,news,hot} = this.state
    return (
      <div className='home'>
        <Lunbo lunbotu={lunbotu}></Lunbo>

        <div className="hot" id="hotproduct">
        <NavLink to="/Shop">
            <div className="t-title">
                <img src={window.tubase+"24-50.png"}></img>
                <span>HOT</span>
                <p className="text">热门产品</p>
            </div>
            </NavLink>
            <NavLink to="/Shop"  className="more-click">MORE <img src={window.tubase+"11-11.png"}></img></NavLink>
            <Carousel autoplay autoplaySpeed={2000} draggable className='hotlunbo'>
            {hot.map((value,index)=>{
              return <div key={index}>
                <Tabs defaultActiveKey="1"  type="card" tabPosition="bottom">
                      {value.xxk.map((value,index)=>{
                        const rmimg=<img src={window.tubase+value.fmimg}></img>
                        return  <TabPane tab={rmimg}  key={index}>
                                  <NavLink to={"/Shopone/"+value.id}>
                                    <img src={window.tubase+value.hotimg}></img>
                                  </NavLink>
                                </TabPane>
                    })}
                </Tabs>
              </div>
                  })}
            </Carousel>
        </div>

{/* 质讯 */}
        <div className="news">
            <div className="t-title">
                <img src={window.tubase+"24-50.png"}></img>
                <span>NEWS</span>
                <p className="text">资讯</p>
            </div>
            <NavLink to="/News"  className="more-click">MORE <img src={window.tubase+"11-11.png"}></img>                        
            </NavLink>


            <div className='content'>
              <div className='newscontent'>
                  <div className='topriitems items'>
                    <div className="img">
                        <img src={window.tubase+"news3.png"}></img>
                    </div>
                    <div className="con">
                        <p className="time">2022.01.04</p>
                        <h6 className="tit"><NavLink to="/Newsone/1">RK98三模RGB无线键盘</NavLink></h6>
                        <p className="con-t">TTC联合定制 七彩红轴</p>
                        <div><svg t="1655514492573" className="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="1556" width="30" height="30"><path d="M96 512a32 32 0 0 1 32-32h768a32 32 0 0 1 0 64H128a32 32 0 0 1-32-32z" fill="#DAB866" p-id="1557"></path></svg></div>
                        <p className="cont-x">全键热插拔丨硅胶夹心垫丨消音底棉丨精调已润卫星轴</p>

                        
                        <NavLink to="/Newsone/1" className="newsmore">MORE <img src={window.tubase+"11-11.png"}></img></NavLink>
                    </div>
                  </div>
                  <div className='toplftitems items'>
                    <NavLink to="/Newsone/2">
                      <img src={window.tubase+"news1.png"}></img>
                    </NavLink>
                  </div>
              </div>
              <div className='newscontent'>
              <div className='toplftitems items'>
                  <NavLink to="/Newsone/3">
                      <img src={window.tubase+"news2.png"}></img>
                  </NavLink>
                  </div>
                  <div className='topriitems items'>
                    <div className="img">
                      <NavLink to="/Newsone/3">
                        <img src={window.tubase+"news4.png"}></img>
                      </NavLink>
                    </div>
                    <div className="con">
                        <p className="time">2022.01.04</p>
                        <h6 className="tit"><NavLink to="/Newsone/2">RK98三模RGB无线键盘</NavLink></h6>
                        <p className="con-t">TTC联合定制 七彩红轴</p>
                        <div><svg t="1655514492573" className="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="1556" width="30" height="30"><path d="M96 512a32 32 0 0 1 32-32h768a32 32 0 0 1 0 64H128a32 32 0 0 1-32-32z" fill="#DAB866" p-id="1557"></path></svg></div>
                        <p className="cont-x">全键热插拔丨硅胶夹心垫丨消音底棉丨精调已润卫星轴</p>

                        
                        <NavLink to="/Newsone/2" className="newsmore">MORE <img src={window.tubase+"11-11.png"}></img>                      </NavLink>
                    </div>
                  </div>

              </div>
            </div>
            

        </div>
{/* 评测 */}
        <div className="none">
            <div className="t-title">
                <img src={window.tubase+"24-50.png"}></img>
                <span>EVALUATION</span>
                <p className="text">评测</p>
            </div>
            <a href="http://www.rkgaming.com/products/1/" className="more-click">MORE <img src={window.tubase+"11-11.png"}></img></a>

              <div className='nonecon'>
                <div className='noneleft'>
                <NavLink to="/Evaluation/1">
                  <div className='nonelefttu'>
                    <img src={window.tubase+"none1.png"}></img>
                  </div>
                  <div className='noneleftcon'>
                    <div className="noneleftcon-title">
                        <img src={window.tubase+"24-50.png"}></img>
                        <span>RK98三模RGB无线键盘</span>
                        <p className="text">RK与TTC联合定制丨七彩红轴</p>
                    </div>
                    <Button>查看详情</Button>
                  </div>
                  </NavLink>
                </div>

                <div className='noneleft'>
                <NavLink to="/Evaluation/2">
                  <div className='nonelefttu'>
                    <img src={window.tubase+"none2.png"}></img>
                  </div>
                  <div className='noneleftcon'>
                    <div className="noneleftcon-title">
                        <img src={window.tubase+"24-50.png"}></img>
                        <span>RK96三模无线机械键盘</span>
                        <p className="text">一键音量调节丨磁吸手托</p>
                    </div>
                    <Button>查看详情</Button>
                  </div>
                  </NavLink>
              </div>
            </div>

        </div>
        
      </div>
    )
  }
}
