import React, { Component } from 'react'
import { Route, Switch, NavLink } from 'react-router-dom'
import Home from './pages/Home/Home'
import News from './pages/News/News'
import Shop from './pages/Shop/Shop'
import Newsone from './pages/Newsone/Newsone'
import Shopone from "./pages/Shopone/Shopone"
import Evaluation from './pages/Evaluation/Evaluation'
import Page404 from './pages/Page404/Page404'
import "./App.css"

export default class App extends Component {

    state={
        isdaoh:0,
      }
    // 导航
    tiaoxs=()=>{
        this.setState({isdaoh:0})
      }
    daoh=()=>{
        const {isdaoh} = this.state
        const a = isdaoh==0 ? '1':'0'
        this.setState({isdaoh:a})
      }


    //在componentDidMount，进⾏scroll事件的注册，绑定⼀个函数，让这个函数进⾏监听处理
    componentDidMount() {
        window.addEventListener('scroll', this.bindHandleScroll)
    }
    //在componentWillUnmount，进⾏scroll事件的注销
    componentWillUnmount() {
        window.removeEventListener('scroll', this.bindHandleScroll);
    }
    bindHandleScroll = (event) => {
        // 滚动的⾼度
        const scrollTop = (event.srcElement ? event.srcElement.documentElement.scrollTop : false)
            || window.pageYOffset
            || (event.srcElement ? event.srcElement.body.scrollTop : 0);
        this.setState({
            hasVerticalScrolled: scrollTop > 700,
        })
    }
      
  render() {
    const {isdaoh ,hasVerticalScrolled} = this.state
    return (
      <div>
          {/* 页头 */}

            <div className={hasVerticalScrolled ? 'header1 header':'header'}>
                <NavLink exact to="/" className="logo" activeClassName='curr'>
                    <img src={window.tubase+"20210617175749_9723.png"}></img>
                </NavLink>
                <div className='headermenu' onClick={()=>this.daoh()}>
                    <div className='center' datatype='true'>
                        <span className={isdaoh==0 ? '':'xiaoshi'}></span>
                        <span className={isdaoh==0 ? '':'xiaoshi'}></span>
                        <p className={isdaoh==0 ? 'xiaoshi':''+"iconfont"}>x</p>
                        <p>菜单</p> 
                    </div>
                </div>
                <div className="bottom">
                    <a href="#"><svg t="1655202005794" className="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="1453" width="16" height="16"><path d="M1022.08 716.928V300.48l-217.28 217.344 217.28 199.04M655.36 667.2a188.16 188.16 0 0 1-133.888 55.488c-50.56 0-98.176-19.712-133.952-55.488L270.976 550.592 20.864 779.84v38.4c0 50.24 40.832 91.072 91.008 91.072h819.2c50.176 0 91.008-40.832 91.008-91.008v-39.68l-249.536-228.608-117.12 117.12" fill={hasVerticalScrolled ? '#000000':'#ffffff'}  p-id="1454"></path><path d="M932.928 128h-819.2C63.616 128 22.848 168.832 22.848 219.008v19.008l398.784 398.912c27.2 27.136 63.36 42.112 101.76 42.112 38.464 0 74.624-14.976 101.76-42.112L1024 238.016v-19.008C1024 168.832 983.04 128 932.928 128M20.864 300.48v417.6L238.72 518.4 20.864 300.48" fill={hasVerticalScrolled ? '#000000':'#ffffff'} p-id="1455"></path></svg></a>

                    <a href="#"><i className="iconfont">EN</i></a>
                </div>
            </div>
            <div className={isdaoh==0 ? 'nomenu':'menu'} >

                <div className="menu-content">
                    <ul className="one">
                        <li>
                            <NavLink exact to="/" activeClassName='curr' onClick={()=>this.tiaoxs()}>
                                <div className="item">
                                    <span className="name">首页</span>
                                </div>
                                <p>HOME</p>
                            </NavLink>
                        </li>
                        <li data-id="1">
                            <NavLink to="/Shop" activeClassName='curr' onClick={()=>this.tiaoxs()}>
                                <div className="item">
                                    <span className="name">产品</span>
                                </div>
                                <p>PRODUCT</p>
                            </NavLink>
                        </li>

                        <li>
                            <NavLink to="/News" activeClassName='curr' onClick={()=>this.tiaoxs()}>
                                <div className="item">
                                    <span className="name">质讯</span>
                                </div>
                                <p> EVENT</p>
                            </NavLink>
                        </li>

                        <li data-id="2">
                            <a href="#">
                                <div className="item">
                                    <span className="name">评测</span>
                                </div>

                                <p>EVALUATION</p>
                            </a>

                        </li>

                        <li>
                            <a href="#">
                                <div className="item">
                                    <span className="name">下载专区</span>
                                </div>

                                <p>download</p>
                            </a>

                        </li>

                        <li>
                            <a href="#/">
                                <div className="item">
                                    <span className="name">技术支持</span>
                                </div>

                                <p>support</p>
                            </a>
                        </li>
                        <li>
                            <a href="#">
                                <div className="item">
                                    <span className="name">在线说明书</span>
                                </div>
                                <p>books</p>
                            </a>
                        </li>
                        
                    </ul>
                    <div className="two">
                    <ul data-id="1">
                            
                            <li child-id="0">
                                <a href="#">
                                <span className="name">机械键盘</span>
                                </a>
                            </li>
                            
                    </ul>

                    <ul data-id="2">

                        <li child-id="3">
                            <a href="#">
                            <span className="name">资讯</span>
                            </a>
                        </li>

                        <li child-id="3">
                            <a href="#">
                            <span className="name">精选产品评价</span>
                            </a>
                        </li>

                </ul>
                    </div>
                </div>

            </div>


            <Switch>
                <Route path="/" exact component={Home}></Route>
                <Route path="/News" component={News}></Route>
                <Route path="/Shop" component={Shop}></Route>
                <Route path="/Newsone/:id" component={Newsone}></Route>       
                <Route path="/Shopone/:id" component={Shopone}></Route>
                <Route path="/Evaluation/:id" component={Evaluation}></Route>
                <Route path="*" component={Page404}></Route>
            </Switch>


            {/* 页尾 */}
            <div className="footer">
                <div className="left">
                    <img className='logo' src={window.tubase+"274-123.png"}></img>

                    <p>
                        <a href="#" target="_blank">©2021  RK 京ICP备2021013293号</a><br/>
                        <a target="_blank" href="#">
                            京公网安备 11011402011905号
                        </a>
                    </p>

                </div>
                <div className="right">

                    <ul>
                        <li>
                            <a href="#">联系我们</a>
                        </li>

                        <li>
                            <a href="#">公司介绍</a>
                        </li>

                        <li>
                            <a href="#">邮件咨询</a>
                        </li>

                        <li>
                            <a href="#">商务合作</a>
                        </li>

                    </ul>

                    <ul>
                        <li>
                            <a href="#">技术支持</a>
                        </li>

                        <li>
                            <a href="#">常见问题</a>
                        </li>

                        <li>
                            <a href="#">产品手册</a>
                        </li>

                        <li>
                            <a href="#">售后支持</a>
                        </li>

                    </ul>

                    <ul>
                        <li>
                            <a href="#">下载专区</a>
                        </li>

                        <li>
                            <a href="#">驱动下载</a>
                        </li>

                        <li>
                            <a href="#">固件下载</a>
                        </li>

                        <li>
                            <a href="#">手册下载</a>
                        </li>

                    </ul>

                    <ul>
                        <li>
                            <a href="#">友情链接</a>
                        </li>
                        
                            
                            <li>
                                <a href="#">亚马逊</a>
                            </li>
                            
                        
                            
                            <li>
                                <a href="#">京东旗舰店</a>
                            </li>
                            
                        
                            
                            <li>
                                <a href="#">天猫旗舰店</a>
                            </li>
                            
                        

                    </ul>
                </div>
            </div>

      </div>
    )
  }
}
