import React, { Component } from 'react'
import { NavLink } from 'react-router-dom'
import "./News.css"
import { Col, Row } from 'antd';


export default class News extends Component {
  state={
    shoplist:[
      {id:1,images:"shoplist1.jpg",newstitle:"首发TTC七彩红轴 RK98换芯登场",riqi:"2022.01.04",jj:"RK96余热未散，RK98三模键盘随即登场，首发TTC七彩红轴，丝滑线性手感，轴体尼龙PA彩壳，完美同步键帽配色，诸多新升级功能，98配列新标杆！ 勇士，微光，水绿PBT双色注塑键帽，新配色，新气象，总有一款适合你！ 独家首发定制TTC七彩红轴，红色轴芯多色外壳，还带导光柱，三色三轴同手感，完美对应键帽主题色，参数接近常规红轴，纵享丝滑线性手感！"},
      {id:2,images:"shoplist2.jpg",newstitle:"全新98配列带滚轮 RK96 RGB三模无线机械键盘登场",riqi:"2022.01.04",jj:"对于想要数字区且紧凑配列键盘的用户来说，只有98配列才能满足他们的需求了，这也是以前RK键盘产品独缺的一环，但现在再也不需要眼馋别家的98键盘了，因为RK的98配列紧凑机械键盘也要来了！"},
      {id:3,images:"shoplist3.jpg",newstitle:"精致无线96配列 RK860",riqi:"2022.01.04",jj:"配置和价格，RK从不让人失望。三模无线的96配列量产机械键盘，更是目前独此一家。而且价格便宜，省下来的钱可以给它配一套个性键帽，颜值瞬间就上来了。毕竟，众所周知，得益于紧凑布局，96简直就是完美！"},
    ], 
  }
  componentDidMount(){
    document.documentElement.scrollTop = document.body.scrollTop =0;//跳转后回到顶部
  }
  render() {
    const {shoplist} = this.state
    return (
      <div className='news'>
        <div className="t-title">
            <img src={window.tubase+"24-50.png"}></img>
            <span>NEWS</span>
            <p className="text">资讯</p>
        </div>
        <img src={window.tubase+"newslist1.png"}></img>
        <div className='shoptitle'>
          <div>
            <a>资讯</a>
          </div>
        </div>
        <div className='search'>
          <input type="text" placeholder='请输入'></input>
          <svg t="1655617071866" className="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="1396" width="16" height="16"><path d="M680.728354 651.326209c121.274064-137.208988 116.320239-346.908988-14.939247-478.166427-136.444579-136.433322-357.662913-136.433322-494.094188 0-136.443555 136.442532-136.443555 357.661889 0 494.105445 131.241067 131.253346 340.927763 136.204102 478.149031 14.942317l265.63187 265.63187 30.889521-30.877241L680.728354 651.326209zM649.273968 622.002346l-28.658713 28.668946c-120.345925 105.622596-303.678394 101.031021-418.524049-13.812587-119.651101-119.651101-119.651101-313.648466 0-433.299567C321.742307 83.909062 515.740696 83.909062 635.39282 203.569372 750.211868 318.380234 754.826979 501.656421 649.273968 622.002346z" p-id="1397"></path></svg>
        </div>
        <div className='shoplist'>
          <ul>
          {shoplist.map((value,index)=>{
          return  <li key={index} className="shoplistge">
                    <NavLink to={"/Newsone/"+value.id} className="newslist">
                      <div className='shoplistimg'><img src={window.tubase+value.images}></img></div>
                      <div className='newslistri'>
                        <h6>{value.newstitle}</h6>
                        <span>{value.riqi}</span>
                        <p>{value.jj}</p>
                      </div>
                    </NavLink>
                  </li>
          })}
          </ul>
        </div>



      </div>
    )
  }
}
