import React, { Component } from 'react'
import { NavLink } from 'react-router-dom'
import "./Evaluation.css"

export default class Evaluation extends Component {
    state={
        evaluation:[
            {id:1,evaluationtitle:"RK98",riqi:"2022·01·04",price:"250-390元",lj:"//player.bilibili.com/player.html?aid=422125832&bvid=BV1b341147ff&cid=456219779&page=1",tu:"news3.png",jjie:" P帽丨轴润座热芯丨底部精调丨硅胶粘芯垫丨消音棉",isshopone:8},
            {id:2,evaluationtitle:"RK96",riqi:"2022·01·04",price:"299-499元起",lj:"//player.bilibili.com/player.html?aid=208860400&bvid=BV1Jh41187a7&cid=430725435&page=1",tu:"news4.png",jjie:"滚轮音量调节丨磁吸手托丨三模连接丨全键热插拔",isshopone:7},
          ], 
          cphq:[], 
      }
      componentDidMount(){
        const  {id,}=this.props.match.params
        document.documentElement.scrollTop = document.body.scrollTop =0;//跳转后回到顶部

        const newcpone=this.state.evaluation.find(value=>value.id==id)
        this.setState({cphq:newcpone})

      }
      fh=()=>{
        this.props.history.go(-1)
      }
      render() {
        return (
          <div className='shopone'>
            <div className="t-title">
                <img src={window.tubase+"24-50.png"}></img>
                <span>产品评测</span>
                <p className="text">EVALUATION</p>
            </div>
    
            <div className='shoptitle'>
                <p>产品评测</p>
                <span onClick={this.fh}>返回</span>
            </div>
            <div className='ship'>
                <h1>{this.state.cphq.evaluationtitle}</h1>
                <p>{this.state.cphq.riqi}</p>
                <iframe className='bilbili' src={this.state.cphq.lj} scrolling="no" border="0" framespacing="0"> </iframe>
            </div>
            <NavLink to={"/Shopone/"+this.state.cphq.isshopone}>
                <div className='cpxq'>
                    <div className="img">
                        <img src={window.tubase+this.state.cphq.tu}></img>
                    </div>
                    <div className="con">
                        <h6 className="tit">{this.state.cphq.evaluationtitle}</h6>
                        <p className="con-t">价格： {this.state.cphq.price}</p>
                        <p className="con-t">简介： {this.state.cphq.jjie}</p>

                        <div className='chak'>
                            <a>查看产品</a>
                        </div>
                        
                    
                    </div>
                </div>            
            </NavLink>
          </div>
        )
      }
}
