import React, { Component } from 'react';
import { Carousel } from 'antd';
import { NavLink } from 'react-router-dom'
import "./Lunbo.css"

export default class Lunbo extends Component {
  next() {
		this.slider.slick.slickNext();
	}
	prev() {
		this.slider.slick.slickPrev();
	}
  render() {
    const {lunbotu} = this.props
    return (
        <div className='lunbo'>
          <Carousel autoplay autoplaySpeed={2000} draggable >
            {lunbotu.map((value,index)=>{
              return <div key={index}>
                        <NavLink to={"/Shopone/"+value.id}>
                          <img src={window.tubase+value.tu}></img>
                       </NavLink>
                     </div>
            })}
          </Carousel>
        </div>
    )
  }
}
